import React, { useEffect } from "react";
import useAuth from "../api/auth";
import SpotifyWebApi from "spotify-web-api-node";
import { useState } from "react";
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Button from '@mui/material/Button';

import Explanation from "./explanation";

const spotifyApi = new SpotifyWebApi({
  clientId: process.env.REACT_APP_CLIENT_ID,
});

function Viz({code}) {
  const SAVED_TRACK_LIMIT = 9950;
  const SAVED_TRACK_RETRIEVAL_LIMIT = 50;
  const POPULARITY_THRESHOLD = 20;

  const accessToken = useAuth(code);
  const [savedTracks, setSavedTracks] = useState([]);
  const [checkedList, setCheckedList] = useState([]);

  function parseSavedTracks() {
    let filteredTracks = savedTracks.filter(data => data.track.popularity <=
      POPULARITY_THRESHOLD).map((data) => {
        return (
          <FormControlLabel
            control={<Checkbox onChange={d => {
              if (data.track.id in checkedList) {
                for (let i = 0; i < checkedList.length; i++) {
                  if (checkedList[i] === data.track.id) {
                    setCheckedList(checkedList.splice(i, 1));
                  }
                }
              } else {
                checkedList.push(data.track.id)
              }
            }}
            style={{
              color: "white"
            }}/>}
            label={data.track.artists[0].name + " - " + data.track.name +
              " (" + data.track.popularity + ")"}
            key={data.track.id} />
        );
      });
    filteredTracks = [...new Set(filteredTracks)];
    return (
      <FormGroup>
        {filteredTracks}
      </FormGroup>
    )
  }

  /**
   * Calls Spotify API to get all saved tracks from user.
   */
  function getSavedTracks() {
    let offset = 0;
    while (offset !== SAVED_TRACK_LIMIT) {
      spotifyApi.getMySavedTracks({
        limit: SAVED_TRACK_RETRIEVAL_LIMIT,
        offset: offset
      })
      .then((data) => {
        setSavedTracks(savedTracks => [...savedTracks, ...data.body["items"]]);
      });
      offset += 50;
    }
  }

  function removeTracks() {
    spotifyApi.removeFromMySavedTracks(checkedList)
    .then(() => {
      console.log("Removed!");
    }).catch((error) => {
      console.log(error);
    });
  }

  useEffect(() => {
    if (!accessToken) {
      return;
    }

    spotifyApi.setAccessToken(accessToken);

    getSavedTracks();
  }, [accessToken]);

  return (
    <div className="App-header">
      choose songs to delete from your library or make a playlist of your
        most neglected songs
      <br />
      <br />
      <Explanation />
      <br />
      {savedTracks.length === 0 ? null : parseSavedTracks()}
      <br />
      <Button
        variant="contained"
        color="secondary"
        size="large"
        onClick={removeTracks}
        href="https://spotify-cleanup.herokuapp.com/end"
        disableElevation
      >
        SUBMIT
      </Button>
    </div>
  );
};

export default Viz;