import React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

function Explanation() {

  return (
    <Accordion style={{
      color: "#fff",
      backgroundColor: "#383838"
      }}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon style={{ color:"#fff" }}/>}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography variant="h6">what's with the number next to the song?</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Typography>
          The number is calculated by Spotify to represent how popular the
           track is based on your listening habits. The score can range from 0
           to 100, with 0 being least popular to 100 being most popular. Songs
           shown in this list are tracks with popularity less than or equal to
           20. The data lags a bit, so some songs you played on repeat recently
           may show up. Sorry about that :/
        </Typography>
      </AccordionDetails>
    </Accordion>
  )

}

export default Explanation;