const CLIENT_ID = process.env.REACT_APP_CLIENT_ID;
const REDIRECT_URI = "https://spotify-cleanup.herokuapp.com/viz"
const AUTH_ENDPOINT = "https://accounts.spotify.com/authorize"
const scopes = [
  "streaming",
  "user-read-email",
  "user-read-private",
  "user-library-read",
  "user-library-modify",
  "user-top-read",
];

export const loginUrl = `${AUTH_ENDPOINT}?client_id=${CLIENT_ID}&response_type=code&redirect_uri=${REDIRECT_URI}&scope=${scopes.join(
  "%20"
)}`;
