import Button from '@mui/material/Button';
import { loginUrl } from "../api/spotify";

function Home() {
  return (
    <div className="App">
      <header className="App-header">
        <h1 style={{color: "white"}}>Spotify says you're neglecting these songs</h1>
        <h2 style={{color: "white"}}>let's clean out your music library</h2>
        <br />
        <Button
          variant="contained"
          color="secondary"
          size="large"
          href={loginUrl}
          disableElevation
        >
          LOGIN WITH SPOTIFY
        </Button>
      </header>
    </div>
  );
}

export default Home;